.title {
  text-align: center;
  color: #3010e0;
  font-size: 70px;
  font-weight: 700;
  margin-right: 25px;
  text-shadow: -5px -5px 10px rgba(41, 31, 184, 0.59);
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.section {
  padding-top: 10%;
}

.text {
  margin-top: 20px;
  text-align: center;
  color: #0bbb1a;
}

.button_add {
  font-size: 20px;
  font-weight: 800;
  color: rgba(110, 4, 4, 0.8);
  margin-top: 30px;
  padding: 15px;
  border: none;
  box-shadow: 5 0 2px #333;
  cursor: pointer;

  border-radius: 5px;
  transition-duration: 400ms;
}
